export default "ion-toolbar {\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  --background: transparent;\n  --color: white;\n}\n\nion-toolbar ion-button,\nion-toolbar ion-back-button,\nion-toolbar ion-menu-button {\n  --color: white;\n}\n\n.about-header {\n  position: relative;\n  width: 100%;\n  height: 30%;\n}\n\n.about-header .about-image {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  background-position: center;\n  background-size: cover;\n  background-repeat: no-repeat;\n  opacity: 0;\n}\n\n.about-header .madison {\n  background-image: url(/assets/img/about/madison.jpg);\n}\n\n.about-header .austin {\n  background-image: url(/assets/img/about/austin.jpg);\n}\n\n.about-header .chicago {\n  background-image: url(/assets/img/about/chicago.jpg);\n}\n\n.about-header .seattle {\n  background-image: url(/assets/img/about/seattle.jpg);\n}\n\n.about-info {\n  position: absolute;\n  margin-top: -10px;\n  border-radius: 10px;\n  background: var(--ion-background-color, #fff);\n}\n\n.about-info h3 {\n  margin-top: 0;\n}\n\n.about-info ion-list {\n  padding-top: 0;\n}\n\n.about-info p {\n  line-height: 130%;\n  color: var(--ion-color-dark);\n}\n\n.about-info ion-icon {\n  -webkit-margin-end: 32px;\n          margin-inline-end: 32px;\n}\n\n/*\n * iOS Only\n */\n\n.ios .about-info {\n  --ion-padding: 19px;\n}\n\n.ios .about-info h3 {\n  font-weight: 700;\n}\n\n.analytis-number {\n  background: var(--ion-continue-background);\n}\n\n.popover-button {\n  position: relative;\n  background-color: #007bff;\n  color: white;\n  border: none;\n  padding: 10px 20px;\n  font-size: 16px;\n  border-radius: 5px;\n  cursor: pointer;\n}\n\n/* Popover Container */\n\n.popover {\n  position: absolute;\n  top: 110%;\n  left: 50%;\n  transform: translateX(-50%);\n  background-color: white;\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\n  border-radius: 8px;\n  overflow: hidden;\n  opacity: 0;\n  visibility: hidden;\n  transition: opacity 0.3s ease, visibility 0.3s ease;\n  z-index: 10;\n  width: 200px;\n}\n\n/* Show Popover on Hover */\n\n.popover-button:hover .popover,\n.popover-button:focus-within .popover {\n  opacity: 1;\n  visibility: visible;\n}\n\n/* Links in Popover */\n\n.popover a {\n  display: block;\n  padding: 10px 15px;\n  text-decoration: none;\n  color: #007bff;\n  font-size: 14px;\n  border-bottom: 1px solid #ddd;\n}\n\n/* Remove border for the last link */\n\n.popover a:last-child {\n  border-bottom: none;\n}\n\n/* Link Hover Effect */\n\n.popover a:hover {\n  background-color: #f0f0f0;\n}\n\n/* Add Arrow */\n\n.popover::before {\n  content: \"\";\n  position: absolute;\n  top: -8px;\n  left: 50%;\n  transform: translateX(-50%);\n  border-width: 8px;\n  border-style: solid;\n  border-color: transparent transparent white transparent;\n}\n\n.active-tab ion-icon {\n  color: blue !important;\n}\n\n.panel {\n  --background: #db332e;\n  --color: white;\n}\n/*# sourceMappingURL=data:application/json;base64, */"