import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

import { SettingsPage } from "./settings";

const routes: Routes = [
  {
    path: "",
    component: SettingsPage,
    children: [

      {

        path: "home",
        loadChildren: () => import('../home/home.module').then(m => m.HomePageModule)
      },
      {
        path: "csd-car-brands",
        loadChildren: () => import('../category/category.module').then(m => m.CategoryPageModule)
      },

      {
        path: "all-csd-cars",
        loadChildren: () => import('../menu/menu.module').then(m => m.MenuPageModule),
      },
      // {
      //   path: "all-csd-cars/cars/bike",
      //   loadChildren: () => import('../menu/menu.module').then(m => m.MenuPageModule)
      // },
      {
        path: "all-csd-cars/:id",
        loadChildren: () => import('../menu/menu.module').then(m => m.MenuPageModule)
      },
      {
        path: "csd-car-details/:id",
        loadChildren: () => import('../productDetails/productDetails.module').then(m => m.ProductDetailsPageModule)
      },
      {
        path: "csd-car-more-pages",
        loadChildren: () => import('../contact/contact.module').then(m => m.ContactPageModule)
      },
      {
        path: 'show-interest/:service/:car',
        loadChildren: () => import('../showInterest/showInterest.module').then(m => m.ShowInterestPageModule)
      },
      {
        path: 'search/:from',
        loadChildren: () => import('../search/search.module').then(m => m.SearchPageModule)
      },
      
       
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SettingsPageRoutingModule {}
